	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Cybercriminals are often perceived as having super powers to get into your systems and steal your money and data. The vast majority are just following simple cookbook recipes to take advantage of laziness, sloppiness and an failure to understand what might be risky behaviour. Defences against cybercrime do not have to be hi-tech or even very complex.\nThis talk discusses various types of cybercrime and gives some practical advice as to how to make yourself a little safer.","meta":{"duration":"PT51M4S"},"presenter":{"name":"Richard Clayton"},"source":{"id":"624S9ly09Xk","provider":"youtube"},"slug":"a-practical-guide-to-cybercrime","tag":["GOTO Conference","University of Cambridge","Cybercrime","Security","Privacy"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/624S9ly09Xk/default.jpg","height":90,"width":120}},"title":"A Practical Guide to Cybercrime"}},{"node":{"description":"For Google's Security and Privacy Month, we are honored to present the real Frank Abagnale, Renowned Cybersecurity And Fraud Prevention Expert, Bestselling Author & Subject of Catch Me If You Can.\n\nHis transformation from one of the world’s most notorious con men to an international cybersecurity expert trusted by the FBI has been mythologized in film and literature – but the takeaways he shares are the real deal.\n\nFrank's contributions to the world of security are immeasurable. He has become a hero to hundreds of public and private sector organizations for his indispensable counsel and strategic insight on safeguarding information systems and combating cyber-fraud.\n\nWith an eye on the latest techniques developed by high-tech criminals to deceive and defraud, Frank leaves audiences with a deep understanding of today’s evolving security landscape, and more importantly, a vision of how to make the world a safer place.","meta":{"duration":"PT1H3M9S"},"presenter":{"name":"Frank Abagnale"},"source":{"id":"vsMydMDi3rI","provider":"youtube"},"slug":"catch-me-if-you-can","tag":["Talks at Google","Security","Hacking"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/vsMydMDi3rI/default.jpg","height":90,"width":120}},"title":"Catch Me If You Can"}}]}},"pathContext":{"slug":"security","tag":"Security"}}

